	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"Overview","depth":2},{"value":"Objective","depth":2},{"value":"About the App","depth":2},{"value":"About the Website","depth":2}],"html":"<h2>Overview</h2>\n<p>With approximately one in four adults suffering from some form of mental illness, a large portion of the population needs help talking about their problems. The challenge was to design a smartphone application aiming to help people with mental health issue’s and promote healing through community and self-help methods. I was also tasked at creating a brand identity guideline book and accompanying website for the application.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 85.06666666666666%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"null\"\n        title=\"\"\n        src=\"/the-next-website/static/calcada_reachoutap-8a8d488f18686c98896ac9521dbafed6-53128.png\"\n        srcset=\"/the-next-website/static/calcada_reachoutap-8a8d488f18686c98896ac9521dbafed6-7266b.png 375w,\n/the-next-website/static/calcada_reachoutap-8a8d488f18686c98896ac9521dbafed6-0a3c1.png 750w,\n/the-next-website/static/calcada_reachoutap-8a8d488f18686c98896ac9521dbafed6-53128.png 1500w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>Objective</h2>\n<p>Create a smartphone app that allows for multiple methods of coping, healing, and helping others suffering from mental health issues in a delightful and positive way.</p>\n<h2>About the App</h2>\n<p>With mental health issues impacting one fourth of the population worldwide, and as there are no anonymous, convenient, and personalized applications to help users with mental illness, this is clearly a problem that needs solving. When researching other applications, there is no all-in-one app that has all the features necessary to help users cope with these problems, ranging from self help to help from trained and listened professionals. Some of the features that would be explored include mood tracking, meditations, talking with peers &#x26; trained professionals, and more.</p>\n<h2>About the Website</h2>\n<p>The website was designed as a product page to demonstrate the app features for each section of the app, describing them in detail and utilizing slick animations to keep the users engaged with the site. Users can also go through the site where they can find further resources for mental health support such as free hotlines.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 58.53658536585367%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"null\"\n        title=\"\"\n        src=\"/the-next-website/static/calcada_website-90f86d9074acb519f2cc5d4fac1a7e71-53128.png\"\n        srcset=\"/the-next-website/static/calcada_website-90f86d9074acb519f2cc5d4fac1a7e71-7266b.png 375w,\n/the-next-website/static/calcada_website-90f86d9074acb519f2cc5d4fac1a7e71-0a3c1.png 750w,\n/the-next-website/static/calcada_website-90f86d9074acb519f2cc5d4fac1a7e71-53128.png 1500w,\n/the-next-website/static/calcada_website-90f86d9074acb519f2cc5d4fac1a7e71-91640.png 2250w,\n/the-next-website/static/calcada_website-90f86d9074acb519f2cc5d4fac1a7e71-16fa0.png 2296w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>To see more about this project, <a href=\"http://www.michaelcalcada.com/reachout.html\">view it here</a>. If you would like to see more of my work visit my website at <a href=\"www.michaelcalcada.com\">www.michaelcalcada.com</a>.</p>","frontmatter":{"title":"ReachOut App","graduate":"Michael Calcada","category":"UI/UX","category2":"Web Design","category3":"Brand Identity","image":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.5,"src":"/the-next-website/static/reachoutcard-8d4e46ee41d08ab0e4ede39574a24345-9c75d.png","srcSet":"/the-next-website/static/reachoutcard-8d4e46ee41d08ab0e4ede39574a24345-5cf12.png 375w,\n/the-next-website/static/reachoutcard-8d4e46ee41d08ab0e4ede39574a24345-1040f.png 750w,\n/the-next-website/static/reachoutcard-8d4e46ee41d08ab0e4ede39574a24345-9c75d.png 1280w","sizes":"(max-width: 1280px) 100vw, 1280px"}}}}}},"pathContext":{"slug":"/work/reachout-app/","name":"/ReachOut App/"}}

